import React, { Component } from "react";
import SearchPage from "./Search";

class Electronics extends Component {
  constructor() {
    super();

    this.state = {
      electronics: []
    };
  }

  componentDidMount() {
    fetch("/api/electronics")
    .then(res=> res.json())
    .then(electronics => {
    this.setState({electronics})
    });
  }

  

  render() {
   
    
    return (
      <div>
        <h1>Electronics!</h1>
        <p>test 123888</p>
        <SearchPage/>
        
      </div>
    );
  }
}

export default Electronics;



